import axios from 'axios';

const BASE_URL = 'http://192.168.1.36:5000';  // Replace with your machine's IP address

export const fetchMenuItems = async () => {
  try {
    const response = await axios.get(`${BASE_URL}/api/items`);
    console.log('API response:', response.data);
    return response.data.items;
  } catch (error) {
    console.error('Error fetching menu items:', error);
    return [];
  }
};

// services/api.js

export const submitOrder = async (order) => {
  try {
    const response = await fetch('http://192.168.1.36:5000/api/orders', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(order)
    });
    return await response.json();
  } catch (error) {
    return { error: error.message };
  }
};

export const submitOrderItem = async (orderItem) => {
  try {
    const response = await fetch('http://192.168.1.36:5000/api/order_items', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(orderItem)
    });
    return await response.json();
  } catch (error) {
    return { error: error.message };
  }
};

export const fetchOrders = async () => {
  try {
    const response = await axios.get('http://127.0.0.1:5000/api/orders');
    return response.data;
  } catch (error) {
    return { error: error.message };
  }
};

